var that;
export default {
	data() {
		return {
			switchChange: false

		}
	},
	onLoad: function(option) {
		that = this;
		try {
			const value = uni.getStorageSync('switchChange');
			if (value) {
				console.log(value);
				that.switchChange = value;
			}
		} catch (e) {
			console.log('change事件错误值为', e)
		}
	},
	methods: {
		switch1Change: function(res) {
			try {
				uni.setStorageSync('switchChange', res.target.value);
			} catch (e) {
				console.log('change事件错误值为', e)

			}
		},
		clearshuju: function(res) {

			uni.showModal({
				title: '清除缓存',
				content: '清理缓存会有可能影响您的使用体验效果，是否要清除缓存？',
				confirmColor: '#65c186',
				success: function(res) {
					if (res.confirm) {
						try {
							uni.showLoading({
								title: '清理中...',
								mask: true,
							});
							setTimeout(function() {
								uni.hideLoading();
								uni.showToast({
									title: '清理成功',
									icon: "success"
								});
							}, 2000);
						} catch (e) {
							console.log('清除缓存错误：' + e);
						}
					}
				}
			});

		},
		aboutapp: function(res) {
			uni.navigateTo({
				url: './aboutapp'
			})
		},
		feedback: function(res) {
			uni.navigateTo({
				url: './feedback'
			})
		},
		outlogin: function(res) {
			uni.showModal({
				title: '退出账号',
				content: '确定要退出当前 账号吗？',
				confirmColor: '#65c186',
				success: function(res) {
					if (res.confirm) {
						try {
							uni.clearStorageSync();
							uni.redirectTo({
								url: '../myinfo/login'
							})
						} catch (e) {
							console.log('清除缓存错误：' + e);
						}
					}
				}
			});
		}
	}
}
